		box-shadow:
			0 0 0.2px #fff,
			0 0 1px #fff,
			0 0 6px rgb(71, 175, 63),
			0 0 14px rgb(71, 175, 63),
			0 0 18px rgb(71, 175, 63),
			0 0 23px rgb(71, 175, 63);
	}

 	0% {
		/* A slightly smaller blur radius */
		box-shadow:
			0 0 0.1px #fff,
			0 0 0.6px #fff,
			0 0 4px rgb(71, 175, 63),
			0 0 10px rgb(71, 175, 63),
			0 0 14px rgb(71, 175, 63),
			0 0 17px rgb(71, 175, 63);
	}
}

/* The progress bar (scroll indicator) */
.progress-bar {
	height: 3px;
	background: rgb(77, 182, 70);
	width: 0%;
	animation: pulsate 0.5s ease-in-out infinite alternate;    
