	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
}

#loadingContainer:after {
	background: url("https://s.cdpn.io/1715/noise-1.png");
	position: absolute;
	content: "";
	z-index: 1;
	opacity: 0.8;
	filter: brightness(0.1);
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
}

/**************************************************************************/
/*****************************    Loaders    ******************************/
/**************************************************************************/

.loading_container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 0;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background-color: rgba(255, 255, 255, 0.1);
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
	text-align: center;
	border-radius: 12px;
	width: 60%;
	padding: 50px 40px 50px 40px;
}

@media (max-width: 1100px) {
	.loading_container.loading_container {
		width: 80%;
	}
}

.loading_song_info_container {
	height: 100vh;
	width: 100vw;
	background-image: url(/Musomatic/static/media/section_2.a5023f45.svg);
	background-repeat: no-repeat;
	background-size: cover;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.loading_song_info_box {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: rgba(255, 255, 255, 0.1);
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
	text-align: center;
	border-radius: 12px;
	width: 60%;
	padding: 50px 40px 50px 40px;
}

/**************************************************************************/
/*****************************    Spinner    ******************************/
/**************************************************************************/

.loadingSpinner {
	width: 50px;
	height: 50px;
	display: grid;
	border: 4px solid #0000;
	border-radius: 50%;
	border-color: #ccc #0000;
	animation: s6 1s infinite linear;
}
.loadingSpinner::before,
.loadingSpinner::after {
	content: "";
	grid-area: 1/1;
	margin: 2px;
	border: inherit;
	border-radius: 50%;
}
.loadingSpinner::before {
	border-color: #6cc027 #0000;
	animation: inherit;
	animation-duration: 0.5s;
	animation-direction: reverse;
}
.loadingSpinner::after {
	margin: 8px;
}

@keyframes s6 {
	100% {
		transform: rotate(1turn);
	}
}

