.submit-btn {
    width: 100%;
    color: #6cc027;
	border: 1px solid #6cc027;
    border-radius: 30px;
	outline: none;
    background-color: #202020;
	font-family: var(--Poppins);
	font-weight: 500;
	position: relative;
	padding: 10px 30px;
	cursor: pointer;
	text-align: center;
	display: block;    
}

.submit-btn:hover {
    color: #202020;
    border: 1px solid #6cc027;
	box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
	background-color: #6cc027 !important;
}

.submit-btn span {
	cursor: pointer;
	display: inline-block;
	position: relative;
	transition: 0.5s;
}

.submit-btn i {
	position: absolute;
	opacity: 0;
	top: 0;
	right: -20px;
	transition: 0.25s;
    font-weight: 700;
	margin-top: 3px;
}

.submit-btn:hover span {
	padding-right: 25px;
}

.submit-btn:hover i {
	opacity: 1;
	right: 0;
}

.submit-btn:focus {
    color: #202020 !important;
	background-color: #6cc027 !important;
	border: 1px solid #6cc027;
	outline: none !important;
}

.submit-btn:disabled,
.submit-btn[disabled] {
	color: #202020 !important;
	background-color: #6cc027 !important;
	border: 1px solid #6cc027;
	outline: none !important;
}
