	content: "";
	z-index: 0;
	opacity: 0.7;
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
}

#PageNotFoundContainer:after {
	background: url("https://s.cdpn.io/1715/noise-1.png");
	position: absolute;
	content: "";
	z-index: 1;
	opacity: 0.8;
	filter: brightness(0.1);
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
}

